<template>
    <div class="album-detail">
        <div class="edit-title clearfix" v-if="isEdit">
            <div class="back-btn" @click="editCancel">
                <i class="van-icon van-icon-arrow-left"></i> <span></span>
            </div>
            <span class="choose-all" @click="chooseAll"></span>
        </div>
        <div class="edit-title1" v-if="isEdit"></div>

        <div class="album-title" v-if="!isEdit">
            <img src="../../../assets/img/fixed1.jpg">
            <div class="detail-header" :class="{bgc:isBgc}">
                <div class="back-btn" @click="goPath('/home')" :class="{isBlack:isBgc}">
                    <i class="van-icon van-icon-arrow-left"></i> <span ></span>
                </div>
                <i :class="{isBlack:isBgc}" class="van-icon van-icon-more-o menu-btn" @click="showMnuPopup" v-if="!isEdit"></i>
                <i class="van-icon van-icon-more-o up-btn-bgc" v-if="isBgc"></i>
            </div>

            <div class="title-bottom">
                <span></span>
                <span>1212</span>
                <div class="bottom-right">
                    <i class="van-icon van-icon-eye-o"></i><span> 77</span>
                </div>
            </div>
        </div>
        <div class="up-btn" v-if="!isEdit">
            <van-button icon="arrow-up" type="info"></van-button>
        </div>
        <div class=" clearfix" :class="{bigview:isBigView,photolist:!isBigView}">
            <ul>
                <li v-for="(item,index) in photoArray" :key="index" @click="choosely(index,item.id)">
<!--                    <img :src="item.imgSrc">-->
                    <van-image
                        width="100%"
                        height="100%"
                        lazy-load
                        fit="cover"
                        :src="item.imgSrc"
                    />
                    <div class="checked" v-if="chooseAfterValue.indexOf( item.id )!=-1">
                        <i class="van-icon van-icon-success"></i>
                    </div>
                </li>
            </ul>
        </div>

        <div class="detail-bottom">
            <span>------&nbsp;&nbsp;&nbsp;&nbsp;------</span>
        </div>

        <div class="manage-photo" v-if="isEdit">
            <ul>
                <li @click="movePhoto"><span></span></li>
                <li @click="delPhoto"><span></span></li>
                <li @click="editCancel"><span></span></li>
            </ul>
        </div>

        <van-popup
            v-model="showMenu"
            position="bottom"
        >
            <van-cell title="" is-link icon="edit" @click="editAlbum"/>
            <van-cell title="" is-link icon="setting-o" @click="managePhoto"/>
            <van-cell title="" is-link icon="setting-o" @click="bigView" v-if="!isBigView"/>
            <van-cell title="" is-link icon="setting-o" @click="bigView" v-else/>
        </van-popup>
        <van-image-preview v-model="seePhoto" :images="imgSrcArr" @change="onChange" @close="onClose" closeable >
            <template v-slot:index>{{ index }}</template>
            <template v-slot:cover>
                <div class="viewPhoto-bg" :style="bacImage">
                </div>
            </template>
        </van-image-preview>
    </div>
</template>

<script>
    export default {
        name: "AlbumDetail",
        data() {
            return {
                photoArray:[
                    {
                        imgSrc: 'http://39.105.137.236/group1/M00/00/00/J2mJ7F6kAHuANI8kAAZzxYDyrIU172.jpg',
                        id:1
                    },
                    {
                        imgSrc: 'http://39.105.137.236/group1/M00/00/00/J2mJ7F6kAHuANI8kAAZzxYDyrIU172.jpg',
                        id:2
                    },
                    {
                        imgSrc: 'http://39.105.137.236/group1/M00/00/00/J2mJ7F6kAHuANI8kAAZzxYDyrIU172.jpg',
                        id:3
                    },
                    {
                        imgSrc: 'http://39.105.137.236/group1/M00/00/00/J2mJ7F6kAHuANI8kAAZzxYDyrIU172.jpg',
                        id:4
                    },
                    {
                        imgSrc: 'http://39.105.137.236/group1/M00/00/00/J2mJ7F6kAHuANI8kAAZzxYDyrIU172.jpg',
                        id:5
                    },
                    {
                        imgSrc: 'http://39.105.137.236/group1/M00/00/00/J2mJ7F6kAHuANI8kAAZzxYDyrIU172.jpg',
                        id:6
                    },
                    {
                        imgSrc: 'http://39.105.137.236/group1/M00/00/00/J2mJ7F6kAHuANI8kAAZzxYDyrIU172.jpg',
                        id:7
                    },
                    {
                        imgSrc: 'http://39.105.137.236/group1/M00/00/00/J2mJ7F6kAHuANI8kAAZzxYDyrIU172.jpg',
                        id:8
                    },
                    {
                        imgSrc: 'http://39.105.137.236/group1/M00/00/00/J2mJ7F6kAHuANI8kAAZzxYDyrIU172.jpg',
                        id:9
                    },
                    {
                        imgSrc: 'http://39.105.137.236/group1/M00/00/00/J2mJ7F6kAHuANI8kAAZzxYDyrIU172.jpg',
                        id:10
                    },
                    {
                        imgSrc: 'http://39.105.137.236/group1/M00/00/00/J2mJ7F6kAHuANI8kAAZzxYDyrIU172.jpg',
                        id:11
                    },
                    {
                        imgSrc: 'http://39.105.137.236/group1/M00/00/00/J2mJ7F6kAHuANI8kAAZzxYDyrIU172.jpg',
                        id:12
                    },
                    {
                        imgSrc: 'http://39.105.137.236/group1/M00/00/00/J2mJ7F6kAHuANI8kAAZzxYDyrIU172.jpg',
                        id:13
                    },
                    {
                        imgSrc: 'http://39.105.137.236/group1/M00/00/00/J2mJ7F6kAHuANI8kAAZzxYDyrIU172.jpg',
                        id:14
                    },
                    {
                        imgSrc: 'http://39.105.137.236/group1/M00/00/00/J2mJ7F6kAHuANI8kAAZzxYDyrIU172.jpg',
                        id:15
                    },
                    {
                        imgSrc: 'http://39.105.137.236/group1/M00/00/00/J2mJ7F6kAHuANI8kAAZzxYDyrIU172.jpg',
                        id:16
                    },
                    {
                        imgSrc: 'http://39.105.137.236/group1/M00/00/00/J2mJ7F6kAHuANI8kAAZzxYDyrIU172.jpg',
                        id:17
                    },
                    {
                        imgSrc: 'http://39.105.137.236/group1/M00/00/00/J2mJ7F6kAHuANI8kAAZzxYDyrIU172.jpg',
                        id:18
                    },
                    {
                        imgSrc: 'http://39.105.137.236/group1/M00/00/00/J2mJ7F6kAHuANI8kAAZzxYDyrIU172.jpg',
                        id:19
                    },
                    {
                        imgSrc: 'http://39.105.137.236/group1/M00/00/00/J2mJ7F6kAHuANI8kAAZzxYDyrIU172.jpg',
                        id:20
                    },
                    {
                        imgSrc: 'http://39.105.137.236/group1/M00/00/00/J2mJ7F6kAHuANI8kAAZzxYDyrIU172.jpg',
                        id:21
                    },
                ],
                showMenu:false,
                isEdit:false,
                chooseAfterValue:[],
                idArr: [],
                isBgc:false,
                isBigView:false,
                seePhoto:false,
                imgSrcArr:[],
                index: 1,
                bacImage: {
                    backgroundImage: "",
                },
            }
        },
        mounted() {
            window.addEventListener('scroll',this.headerBgc);
        },
        methods: {
            headerBgc() {
                var scrollTop = document.body.scrollTop || document.documentElement.scrollTop;
                var clientHeight=document.documentElement.clientHeight;
                //>
                if(scrollTop>(clientHeight*0.32-120)){
                    this.isBgc = true;
                }else{
                    this.isBgc = false;
                }
            },
            editAlbum() {
                this.$router.push({
                    path:'/edit_album',
                    query:{
                        type:'edit'
                    }
                })
            },
            goPath(url) {
                this.$router.push(url);
            },
            showMnuPopup() {
                this.showMenu = true;
            },
            managePhoto() {
                this.isEdit = true;
                this.isBigView = false;
                this.showMenu = false;
            },
            movePhoto() {
                this.$router.push('/move_photo');
            },
            delPhoto() {
                console.log(this.chooseAfterValue)
                alert(""+this.chooseAfterValue.length)
            },
            editCancel() {
                this.isEdit = false;
                this.chooseAfterValue = [];
            },
            choosely(index,id){
                if(!this.isEdit) {
                    let prevArr = [];
                    let nextArr = [];
                    this.index = this.photoArray[index].id;
                    for (let i = 0; i < this.photoArray.length; i++) {
                        if (i < index) {
                            prevArr.push(this.photoArray[i]);
                        } else {
                            nextArr.push(this.photoArray[i]);
                        }
                    }
                    nextArr.push.apply(nextArr, prevArr);
                    for (let i = 0; i < nextArr.length; i++) {
                        this.imgSrcArr.push(nextArr[i].imgSrc);
                        this.idArr.push(nextArr[i].id)
                    }
                    this.bacImage.backgroundImage = "url(" + this.imgSrcArr[id] + ") "
                    this.seePhoto = true;
                }else{
                    if(this.chooseAfterValue.indexOf( id )!=-1){
                        this.chooseAfterValue.splice( this.chooseAfterValue.indexOf( id ), 1 )
                    }else{
                        this.chooseAfterValue.push(id)
                    }
                }
            },
            onChange(index){
                this.index = this.idArr[index];
                this.bacImage.backgroundImage = "url(" + this.imgSrcArr[index] + ") "
            },
            onClose() {
                this.imgSrcArr = [];
            },
            chooseAll() {
                if(this.chooseAfterValue.length != this.photoArray.length) {
                    for(let i=0; i<this.photoArray.length; i++) {
                        this.chooseAfterValue.push(this.photoArray[i].id);
                    }
                }else{
                    this.chooseAfterValue = [];
                }
            },
            bigView() {
                this.isBigView = !this.isBigView;
                this.showMenu = false;
            }
        }
    }
</script>

<style scoped lang="scss">
.album-detail {

    >>>.van-cell {
        padding: 16px 16px;
    }
    > > > .van-image-preview__overlay {
        background-color: rgba(0, 0, 0, 1);
    }

    > > > .van-swipe {
        z-index: 9999 !important;
    }
    .viewPhoto-bg {
        width: 100%;
        height: 100%;
        background: url('../../../assets/img/fixed2.jpg') no-repeat;
        background-size: cover;
        filter: blur(45px) brightness(0.7);;
        transition: linear 0.3s;
        webkit-transform: translateZ(0);
        -moz-transform: translateZ(0);
        -ms-transform: translateZ(0);
        -o-transform: translateZ(0);
        transform: translateZ(0);
    }

    > > > .van-image-preview__cover {
        width: 100%;
        height: 100%;
    }
    .edit-title1 {
        height: 50px;
    }
    .edit-title {
        height: 50px;
        width: 100%;
        font-size: 18px;
        position: fixed;
        top: 0;
        z-index: 999;
        background-color: #fff;
        .back-btn {
            width: 100px;
            height: 50px;
            i {

                color: #000;
                position: absolute;
                top: 15px;
                left: 13px;
            }
            span {
                position: absolute;
                top: 11px;
                left: 34px;
            }
        }

        .choose-all {
            position: absolute;
            right: 15px;
            top: 50%;
            transform: translateY(-50%);
        }

    }
    .album-title {
        position: relative;
        img {
            width: 100%;
            height: 32vh;
            object-fit: cover;
        }
        .bgc {
            background-color: #fff;
        }
        .isBlack {
            color: #323233!important;
        }
        .detail-header {
            transition: all 0.3s;
            position: fixed;
            z-index: 999;
            top: 0;
            height: 56px;
            width: 100%;
            .back-btn {
                font-size: 18px;
                position: absolute;
                top: 18px;
                left: 12px;
                color: #fff;
                width: 60px;
                i {
                    position: absolute;
                    margin-top: 4px;
                }
                span {
                    position: absolute;
                    left: 20px;
                }
            }
            .menu-btn {
                position: absolute;
                top: 18px;
                right: 15px;
                font-size: 24px;
                color: #fff;
            }
            .up-btn-bgc {
                position: absolute;
                top: 18px;
                right: 65px;
                font-size: 24px;
                color: #000;
            }
        }

        .title-bottom {
            height: 60px;
            width: 100%;
            position: absolute;
            bottom: 0;
            color: #fff;
            span:first-child {
                font-size: 20px;
                position: absolute;
                left: 10px;
                top: 0;
            }
            span:nth-child(2) {
                position: absolute;
                left: 10px;
                top: 30px;
                font-size: 12px;
            }
            .bottom-right {
                position: absolute;
                right: 0px;
                width: 100px;
                height: 30px;
                bottom: 0;
                i{
                    position: absolute;
                    left: 0;
                    top: 0;
                }

                span {
                    left: 20px;
                    top: 0;
                }
            }
        }
    }

    .up-btn {
        background-color: pink;
        width: 90%;
        margin: 10px auto;

        .van-button {
            width: 100%;
        }
    }

    .photolist {
        ul {
            list-style: none;
            li {
                float: left;
                width: 33%;
                height: 33vw;
                margin-right: 0.5vw;
                margin-bottom: 0.5vw;
                position: relative;
                /*>>>.van-image {*/
                /*    height: 100%;*/
                /*    img {*/
                /*        width: 100%;*/
                /*        height: 100%;*/
                /*        object-fit: cover;*/
                /*    }*/
                /*    >>>.van-image__error, .van-image__img, .van-image__loading {*/
                /*        object-fit: cover;*/
                /*    }*/
                /*}*/

                .checked {
                    width: 20px;
                    height: 20px;
                    border-radius: 50%;
                    background-color: #1989fa;
                    position: absolute;
                    right: 10px;
                    top: 10px;
                    text-align: center;
                    i {
                        color: #fff;
                        margin-top: 3px;
                    }
                }
            }
            li:nth-child(3n) {
                margin-right: 0;
            }
        }
    }

    .bigview {
        ul {
            list-style: none;
            li {
                width: 100%;
                float: left;
                img {
                    width: 100%;
                }
            }
        }
    }
    .detail-bottom {
        text-align: center;
        margin: 15px 0;
        color: #aaa;
        font-size: 12px;
    }

    .manage-photo {
        height: 50px;
        width: 100%;
        position: fixed;
        bottom: 0;
        background-color: rgba(0,0,0,0.7);
        ul {
            list-style: none;
            li {
                height: 50px;
                color: #fff;
                text-align: center;
                float: left;
                width: 33%;
                margin-right: 0.5%;
                position: relative;
                span {
                    position: absolute;
                    left: 50%;
                    transform: translateX(-50%);
                    top: 14px;
                }
            }
            li:last-child {
                margin-right: 0;
            }

            li:active {
                background-color: rgba(0,0,0,0.4);
            }
        }
    }
}
.clearfix:before,
.clearfix:after {
    content: "";
    /* BFC,  */
    display: table;
}
.clearfix:after {
    clear: both;
}
.clearfix {
    /* *    ie 6 7   *IE67   zoomie67 */
    *zoom: 1;
}
</style>



